            p.mb-0.style___box_2.ajuste___box_2(data-aos="fade-right") La clave de este enfoque est en la diversidad: diversidad de plantas, de insectos, de microorganismos y de funciones ecolgicas. Cuanta ms diversidad exista en el sistema, mayor ser su capacidad de autorregulacin, resiliencia y sostenibilidad. El manejo agroecolgico busca activar procesos internos del agroecosistema, reconociendo que la salud del cultivo depende de la salud del suelo, de las relaciones entre especies y del conocimiento que emerge desde el territorio.
