                        | Promover asociaciones entre especies que se beneficien mutuamente.
                      li.mb-2
                        span.fa-li
                          i.fas.fa-arrow-alt-circle-right
                        | Activar procesos ecolgicos que sostienen la produccin.
                      li.mb-0
                        span.fa-li
                          i.fas.fa-arrow-alt-circle-right
                        | Integrar el conocimiento campesino en el manejo de los cultivos.

    p.mb-5 A continuacin, se presentan algunas prcticas claves para el manejo agroecolgico de cultivos.

    <!-- TITULOS TERCER NIVEL -->
    .caja-titulos-tercer-nivel.mb-5(data-aos="fade-right")
      .col-lg-auto
        figure
          img(src="@/assets/curso/temas/img11.svg" alt='' style="max-width: 60px").m-auto
      .col
        h3 Elaboracin de bioproductos para la nutricin de los cultivos

    .row.align-items-center.m-0.mb-5
      .col-xl.style___box_8.ajuste___box_8(data-aos="fade-right")
        p.mb-0 En agroecologa, la nutricin de los cultivos no se enfoca en alimentar directamente a las plantas, sino en #[b nutrir y activar los microorganismos del suelo] que sostienen su crecimiento. Estos organismos, como hongos, bacterias, lombrices y otros, descomponen la materia orgnica, liberan nutrientes y crean condiciones favorables para el desarrollo vegetal. Por eso, la elaboracin de bioproductos para la nutricin busca #[b fortalecer la vida del suelo, cerrar ciclos de nutrientes y reducir la dependencia de insumos externos].
      .col-xl-auto(data-aos="fade-left")
        figure.d-none.d-xl-block
          img(src="@/assets/curso/temas/img53.png" alt='').m-auto

    <!-- TITULOS CUERTO NIVEL -->
    h4.mb-5.caja-titulos-cuarto-nivel(data-aos="fade-right") Qu son los bioproductos nutricionales?

    p.mb-5 Son insumos lquidos o slidos elaborados a partir de estircol, residuos vegetales, minerales naturales, microorganismos y fermentos. Se aplican al suelo o directamente a las plantas para mejorar su crecimiento, resistencia y productividad. Los tipos comunes de bioproductos son: 

    <!-- SLYDER IMAGENES -->
    .fondo___slider.mb-5
      .px-5
        .ajuste-slider(data-aos="zoom-in")
          SlyderA(tipo="b")
            .row.h-100.justify-content-center.align-items-center
              .col-xl-6.mb-4
                h4 Biofertilizantes lquidos o bioles
                p.mb-0 Preparados con estircol, melaza, ceniza, leche, suero, microorganismos y agua. Se fermentan por varios das y se aplican diluidos.
              .col-xl-6
