                p.mb-0 Elaborados con ortiga, pltano, yuca o guineo. Aportan potasio, fsforo y micronutrientes.
              .col-xl-6
                figure.d-none.d-xl-block
                  img(src='@/assets/curso/temas/img55.png', alt='' style="max-width: 500px").m-auto

            .row.h-100.justify-content-center.align-items-center
              .col-xl-6.mb-4
                h4 Compost
                p.mb-0 Mezcla de residuos orgnicos descompuestos como vegetales, estircol, ceniza y tierra. Mejora la estructura del suelo y aporta nutrientes gradualmente.
              .col-xl-6
