              .col-xl-6
                figure.d-none.d-xl-block
                  img(src='@/assets/curso/temas/img59.png', alt='' style="max-width: 500px").m-auto

    p.mb-5 Existen muchas formas de preparar bioproductos para la nutricin de los cultivos. Su eleccin depende del #[b contexto agroecolgico], los #[b materiales disponibles], el #[b tipo de cultivo] y los #[b saberes locales]. No hay una receta nica: cada territorio #[b adapta y mejora sus prcticas] segn sus condiciones, su experiencia y sus objetivos. Lo importante es avanzar hacia sistemas que #[b nutran la vida del suelo, fortalezcan la autonoma productiva y regeneren la fertilidad desde lo que ya existe en la finca].

    .row.justify-content-center.mb-5
      .col-xl-6
