        h3 Bioproductos para el manejo de organismos asociados

    .fondo___color_5
      .px-5
    
        .row.align-items-center.mb-5
          .col-xl
            p.mb-3 En agroecologa, los organismos asociados (como insectos, hongos, bacterias y otros)  no se dividen en plagas y enemigos, sino que se comprenden como parte de un sistema en equilibrio. El manejo agroecolgico busca regular su presencia sin eliminarla, favoreciendo los organismos benficos y reduciendo los impactos negativos sobre los cultivos. Para ello, se elaboran bioproductos que permiten controlar, repeler o equilibrar poblaciones de forma natural, sin afectar la salud del suelo, el ambiente ni la biodiversidad.
            p.mb-0.style___box_2.ajuste___box_2_6(data-aos="fade-right") Los biopreparados son preparados artesanales elaborados con plantas, minerales, microorganismos y materiales disponibles en la finca. Se aplican para prevenir o reducir daos causados por insectos, hongos, bacterias o virus. Se usan en forma de extractos, fermentos, caldos o trampas, y se adaptan segn el tipo de cultivo y el organismo a manejar.
          .col-xl-auto(data-aos="fade-left")
            figure.d-none.d-xl-block
              img(src="@/assets/curso/temas/img61.png" alt='').m-auto

        p.mb-5 Los tipos comunes de bioproductos para el manejo de organismos asociados son: 

