            h3 Control biolgico

        .row.align-items-center.m-0.mb-5
          .col-xl.style___box_8.ajuste___box_8_1(data-aos="fade-right")
            p.mb-0 El #[b control biolgico] consiste en #[b favorecer la presencia y accin de organismos benficos] que regulan naturalmente las poblaciones de insectos, hongos y otros organismos asociados. En lugar de eliminar, se busca #[b equilibrar]. Se promueve la #[b biodiversidad funcional] mediante refugios, asociaciones vegetales y manejo del hbitat.
          .col-xl-auto(data-aos="fade-left")
            figure.d-none.d-xl-block
              img(src="@/assets/curso/temas/img67.png" alt='').m-auto

