        h4.mb-5.caja-titulos-cuarto-nivel(data-aos="fade-right") Cmo se aplica?

        p.mb-5 La implementacin se fundamenta en prcticas que fortalecen la presencia y accin de organismos benficos dentro del agroecosistema.

        .row.justify-content-center.mb-5
          .col-xl-10
            .row.align-items-center
              .col-xl-auto(data-aos="fade-right")
                figure.d-none.d-xl-block
                  img(src="@/assets/curso/temas/img68.png" alt='').m-auto
              .col-xl
                .style___box_2(data-aos="fade-left")
                  .style___box_5.ajuste___box_5_5
                    ul.fa-ul.estilo___ul.mb-0
                      li.mb-2
                        span.fa-li
                          i.fas.fa-arrow-alt-circle-right
                        | Conservando #[b cercas vivas, flores nativas y vegetacin diversa] que atraiga insectos benficos.
                      li.mb-2
                        span.fa-li
                          i.fas.fa-arrow-alt-circle-right
                        | Identificando y protegiendo #[b enemigos naturales] como mariquitas, avispas parasitoides, araas, aves insectvoras y hongos entomopatgenos.
                      li.mb-2
                        span.fa-li
                          i.fas.fa-arrow-alt-circle-right
                        | Evitando el uso de pesticidas que afecten la fauna til.
                      li.mb-2
                        span.fa-li
                          i.fas.fa-arrow-alt-circle-right
                        | Integrando cultivos que #[b favorezcan la presencia de controladores biolgicos].
                      li.mb-2
                        span.fa-li
                          i.fas.fa-arrow-alt-circle-right
                        | Introduciendo #[b microorganismos] como bacterias y hongos que regulan los patgenos o los insectos antagonistas de los cultivos.
                      li.mb-0
                        span.fa-li
                          i.fas.fa-arrow-alt-circle-right
                        | Introduciendo #[b insectos, huevos o larvas] de depredadores o parasitoides de insectos antagonistas.

        p.mb-5 El control biolgico es ms que una tcnica: es una forma de #[b reconocer y cuidar las relaciones vivas] que sostienen el agroecosistema. Al #[b favorecer la presencia de organismos benficos], se fortalece el equilibrio natural, se reduce la dependencia de insumos externos y se recupera la capacidad del territorio para autorregularse. Cada finca puede #[b identificar, proteger y potenciar su biodiversidad funcional], integrando #[b saberes locales y observacin constante] como base del manejo.

