        .row.align-items-center.m-0.mb-5
          .col-xl.style___box_8.ajuste___box_8_2(data-aos="fade-right")
            p.mb-0 La alelopata es la capacidad de ciertas plantas para liberar compuestos que afectan el crecimiento de otras especies. En agroecologa, esta propiedad se aprovecha para suprimir arvenses, repeler insectos o favorecer cultivos vecinos. Por su parte, las asociaciones funcionales consisten en combinar especies que se benefician mutuamente, lo que permite mejorar el rendimiento de los cultivos y reducir la presin de plagas.
          .col-xl-auto(data-aos="fade-left")
            figure.d-none.d-xl-block
              img(src="@/assets/curso/temas/img69.png" alt='').m-auto
    
