        p.mb-5 La alelopata y las asociaciones funcionales permiten #[b disear cultivos que se acompaan, se protegen y se nutren mutuamente]. Estas prcticas no solo #[b mejoran el rendimiento y reducen plaga]s, sino que tambin expresan una #[b lgica de cooperacin entre especies], coherente con los principios agroecolgicos. Su aplicacin depende del #[b contexto], del #[b conocimiento campesino] y de la #[b capacidad de observar y adaptar combinaciones] segn las condiciones del suelo, el clima y la cultura productiva local.

