            p.mb-0.style___box_2.ajuste___box_2_3(data-aos="fade-right") Su integracin con los cultivos y el paisaje permite #[b cerrar ciclos ecolgicos y mejorar la resiliencia] del sistema. Esta perspectiva se acompaa de prcticas que #[b garantizan el bienestar animal], como la alimentacin con recursos locales, el manejo sanitario preventivo, el pastoreo rotacional y el respeto por los ritmos y necesidades de cada especie.
