    p.mb-5 La #[b alimentacin agroecolgica] se basa en el #[b uso de recursos locales, diversificados y funcionales], con el objetivo de garantizar la #[b nutricin animal] sin depender de concentrados industriales. Esta estrategia permite aprovechar al mximo los recursos del territorio y adaptar la dieta animal a condiciones especficas. Se priorizan los siguientes elementos:

    .row.justify-content-center.mb-5
