        h3 Pastoreo rotacional y silvopastoreo

    p.mb-5 El pastoreo rotacional y el silvopastoreo permiten manejar el espacio de forma estratgica, regenerar el suelo y diversificar la dieta animal. Se organizan parcelas o franjas de pastoreo que se alternan segn el tiempo de recuperacin del forraje.

