    p.mb-5 Esta prctica mejora la fertilidad del suelo, reduce la erosin y fortalece la salud animal. Una de las metodologas ms reconocidas en este enfoque es el #[b Pastoreo Racional Voisin (PRV)], basado en #[b cuatro principios fundamentales]:

