            .row(titulo="Empaque apropiado").ajuste-cajaAcordion
              p.mb-3 Se usan #[b frascos de vidrio, bolsas de tela, papel o recipientes reciclados], siempre que mantengan las semillas #[b libres de humedad y contaminacin].
