              p.mb-3 Las semillas se guardan en espacios #[b frescos, secos, oscuros y protegidos de plagas]. Se recomienda una #[b revisin peridica] para detectar problemas.
            .row(titulo="Rotacin y renovacin").ajuste-cajaAcordion
              p.mb-3 No se deben almacenar semillas por muchos aos sin sembrarlas. Se promueve su #[b uso constante, reproduccin en campo y renovacin gentica], lo que mantiene su vigor y capacidad de adaptacin.

    p.mb-5 Con este conjunto de cuidados, las semillas conservan su p#[b otencial productivo y su valor simblico], fortaleciendo sistemas agrcolas sustentados en la #[b diversidad, el conocimiento local y la soberana campesina].

