        p.mb-5 Las ferias e intercambios son actos de afirmacin cultural y poltica. En ellas se tejen alianzas, se visibiliza la lucha por la tierra y se construyen alternativas frente al modelo agroindustrial. Son espacios donde las semillas regresan a manos de quienes las han cuidado por generaciones, y donde se proyecta un futuro basado en la diversidad, la autonoma y el cuidado del territorio.

